<script setup lang="ts">
import { toRefs } from 'vue'
import { and } from '@vueuse/math'
import { XMarkIcon } from '@heroicons/vue/24/outline'

import { VehicleTitle } from '@/entities/vehicle'

import type { Vehicle } from '@/shared/types'
import { BusIcon } from '@/shared/ui/icons'
import { PAvatar } from '@/shared/ui/PAvatar'
import { PPhoneLink } from '@/shared/ui/PPhoneLink'

export interface Props {
  name: string
  avatar: string | null
  phone: string | null
  // role: string
  vehicleTitle: Vehicle['title'] | null
  selected?: boolean
  disableRemove?: boolean
}

const props = defineProps<Props>()
const { vehicleTitle } = toRefs(props)

export type Emits = {
  remove: []
  removeVehicle: []
}

defineEmits<Emits>()

const hasVehicle = and(vehicleTitle)
</script>

<template>
  <div
    class="
    group flex items-center justify-between gap-4 px-3 py-2 sm:px-4 sm:py-3
    "
    :class="[
      selected ? 'bg-indigo-100/50' : 'border-y border-gray-100',
    ]"
  >
    <div class="flex gap-4">
      <PAvatar
        class="ring-0 transition-transform group-hover:scale-110"
        :src="avatar"
        :placeholder-name="name"
      />

      <div>
        <p class="max-w-28 truncate text-sm font-medium text-gray-900">
          {{ name }}
        </p>

        <div class="flex items-center gap-1">
          <PPhoneLink v-if="phone" :phone="phone" />
          <!-- <p class="text-xs leading-5 text-gray-500">
            {{ role }}
          </p> -->
        </div>

        <p
          v-if="selected"
          class="select-none text-xs leading-5 text-blue-500"
        >
          Please select a vehicle
        </p>

        <VehicleTitle
          v-if="vehicleTitle"
          :title="vehicleTitle"
          removable
          @remove="$emit('removeVehicle')"
          @click.stop
        />
      </div>
    </div>

    <div class="flex items-center gap-4">
      <button
        v-if="!selected && !hasVehicle"
        class="
        flex items-center gap-1 rounded-full border border-blue-600 px-3 py-1
        text-blue-600 transition-colors disabled:border-slate-400
        disabled:text-slate-400 hover:bg-blue-600 hover:text-white
        "
        :disabled="!!hasVehicle"
      >
        <BusIcon class="size-4 " />

        <p class="hidden text-sm font-medium sm:block">
          Add
        </p>
      </button>

      <button
        v-if="!disableRemove"
        class="transition-transform hover:rotate-90"
        @click.stop="$emit('remove')"
      >
        <XMarkIcon class="size-5 text-gray-500" />
      </button>
    </div>
  </div>
</template>
